@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

@layer base {
  body {
    @apply bg-dark-300 text-light-100;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-bold text-light-100;
  }

  a {
    @apply text-primary hover:text-primary-light transition-colors;
  }

  input, select, textarea {
    @apply bg-dark-400 text-light-100 border border-dark-100 rounded px-3 py-2 focus:outline-none focus:ring-1 focus:ring-primary;
  }
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded font-medium transition-all;
  }

  .btn-primary {
    @apply bg-primary text-white hover:bg-primary-dark;
  }

  .btn-secondary {
    @apply bg-secondary text-white hover:bg-opacity-90;
  }

  .btn-outline {
    @apply border border-primary text-primary hover:bg-primary hover:text-white;
  }

  .navbar {
    @apply bg-dark-500 text-light-100 border-b border-dark-100 shadow-game;
  }

  .navbar-item {
    @apply text-light-100 hover:text-primary;
  }

  .card {
    @apply bg-dark-200 rounded-lg shadow-game border border-dark-100 p-4;
  }

  .stats-card {
    @apply card flex flex-col items-center justify-center;
  }

  .stat-value {
    @apply text-4xl font-bold text-primary my-2;
  }

  .stat-label {
    @apply text-sm text-light-200 uppercase tracking-wider;
  }

  .stat-subtitle {
    @apply text-xs text-light-300;
  }

  .match-card {
    @apply bg-dark-400 border border-dark-100 rounded-lg p-4 flex items-start gap-4 transition-all hover:shadow-game;
  }

  .match-card.custom-match {
    @apply border-l-4 border-l-primary;
  }

  .match-card.registered {
    @apply border-l-4 border-l-accent-green;
  }

  .match-card.selected {
    @apply bg-dark-400 bg-opacity-60 border border-primary-dark;
  }

  .badge {
    @apply bg-primary text-white text-xs px-2 py-1 rounded font-medium;
  }

  .badge-success {
    @apply bg-accent-green;
  }

  .badge-warning {
    @apply bg-accent-yellow text-dark-500;
  }

  .badge-error {
    @apply bg-accent-red;
  }

  .error-message {
    @apply bg-accent-red bg-opacity-10 border border-accent-red text-accent-red rounded p-3;
  }

  .spinner {
    @apply w-10 h-10 border-4 border-dark-100 border-t-primary rounded-full animate-spin;
  }
  
  /* PUBG Stats Display Components */
  .stats-container {
    @apply grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-4 mb-6;
  }
  
  .stats-header {
    @apply flex justify-between items-center mb-6 border-b border-dark-100 pb-4;
  }
  
  .stats-tabs {
    @apply flex bg-dark-400 rounded overflow-hidden mb-6;
  }
  
  .stats-tab {
    @apply px-4 py-2 text-light-200 hover:text-light-100 cursor-pointer transition-colors;
  }
  
  .stats-tab.active {
    @apply bg-primary text-white;
  }
  
  .stats-section {
    @apply bg-dark-200 rounded-lg p-4 shadow-game border border-dark-100 mb-8;
  }
  
  .stats-section-title {
    @apply text-xl font-bold mb-4 text-primary;
  }
  
  .kills-value {
    @apply text-accent-red;
  }
  
  .damage-value {
    @apply text-accent-yellow;
  }
  
  .survival-value {
    @apply text-accent-green;
  }
  
  .ratio-value {
    @apply text-primary;
  }
  
  .season-selector {
    @apply bg-dark-400 text-light-100 border border-dark-100 rounded px-3 py-2;
  }
  
  .mode-toggle {
    @apply flex bg-dark-400 rounded overflow-hidden text-sm;
  }
  
  .mode-toggle-item {
    @apply px-3 py-1 cursor-pointer;
  }
  
  .mode-toggle-item.active {
    @apply bg-primary text-white;
  }
}